import { Box, Flex, Step, StepIcon, StepIndicator, StepStatus, Stepper, StepSeparator, Text, useSteps } from "@chakra-ui/react";

const steps = [
  { title: '', description: '  ' },
  { title: '', description: '   ' },
  { title: '', description: ' ' },
  { title: '', description: '  ' },
];

export default function StepperComponents() {
  const { activeStep } = useSteps({
    index: 0,
    count: steps.length,
  });

  return (
    <Box overflowX="auto" minWidth="750px"> {/*        */}
      <Stepper index={activeStep} colorScheme="teal" size="lg">
        {steps.map((step, index) => (
          <Step key={index}>
            <Flex direction="column" align="center" mb={4}>
              <StepIndicator>
                <StepStatus 
                  complete={<StepIcon boxSize={6} color="teal.600" />}
                  incomplete={
                    <Text fontSize="lg" color="gray.100">{index + 1}</Text>
                  }
                  active={
                    <Text fontSize="lg" fontWeight={600} color="teal.600">{index + 1}</Text>
                  }
                />
              </StepIndicator>
              <Box flexShrink='0' mt={2} textAlign="center">
                <Text fontSize="xl" fontWeight={600} color="teal.700">{step.title}</Text>
                <Text fontSize="md" color="gray.700">{step.description}</Text>
              </Box>
            </Flex>
            <StepSeparator />
          </Step>
        ))}
      </Stepper>
    </Box>
  );
}
